.pokedex-page-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.pokedex-list-container {
    width: 20vw;
    height: 100vh;
    border-radius: 2px;
    overflow: hidden scroll;
    font-family: vcr-mono;
    flex: 1 0 auto;
}
.pokedex-list-container::-webkit-scrollbar {
    display: none;
}

.pokemon-selector {
    padding: 10px;
    border-radius: 2px;
    box-shadow: 0px 0px 2px var(--pokeball-top-dark);
    color: black;
    background-color: white;
    position: relative;
    cursor: pointer;
}
