:root {
    --type-normal: #A8A77A;
    --type-fire: #EE8130;
    --type-water: #6390F0;
    --type-electric: #F7D92C;
    --type-grass: #7AC74C;
    --type-ice: #96D9D6;
    --type-fighting: #C22E28;
    --type-poison: #A33EA1;
    --type-ground: #E2BF65;
    --type-flying: #A98FF3;
    --type-psychic: #F95587;
    --type-bug: #A6B91A;
    --type-rock: #B6A136;
    --type-ghost: #735797;
    --type-dragon: #6F35FC;
    --type-dark: #705746;
    --type-steel: #B7B7CE;
    --type-fairy: #D685AD;
}

.pokemon-types-container {
    display: flex;
    flex-direction: row;
    flex-direction: column;
}
.pokemon-types-container > .types-text {
    text-align: center;
    padding: 5px;
    color: black;
    background: var(--pokeball-bot);
    border-radius: 2px;
    box-shadow: 0 0 5px black;
    margin:  5px 0px 5px 0px;
}
.pokemon-types {
    display: flex;
    flex-direction: row;
}  
.pokemon-type {
    width: 100%;
    text-align: center;
    color: var(--pokeball-bot);
    padding: 5px;
    border-radius: 2px;
    text-shadow: 0 0 5px black;
    box-shadow: 0 0 5px black;
}
.pokemon-type + .pokemon-type {
    margin-left: 5px;
}