.measurements-container > .measurement > span + span, .egg-groups-container > span + span, .species-data > span + span, .park-encounter > span + span {
    margin-left: 10px;
}

.measurements-container {
    display: flex;
    justify-content: space-around;
    padding: 4px;
}

.pokemon-species-container {
    background-color: var(--pokeball-bot);
    padding: 5px;
    border-radius: 2px;
    width: 40vw;
    flex: 1 1 auto;
    overflow: hidden scroll;
    font-size: 0.9em;
}
.pokemon-species-container::-webkit-scrollbar, .moves-container::-webkit-scrollbar {
    width: 10px;
}
.pokemon-species-container::-webkit-scrollbar-thumb, .moves-container::-webkit-scrollbar-thumb {
    background-color: var(--pokeball-top);
    border-radius: 20px;
    border: 3px solid var(--pokeball-bot);
}

.species-data {
    display: flex;
    justify-content: space-between;
    padding: 4px;
}

.park-encounter {
    display: flex;
    align-items: center;
}
