:root {
    --category-physical-font: #F67A1A;
    --category-physical: #C92112;
    --category-special-font: white;
    --category-special: #4F5870;
    --category-status-font: #d3d3d3;
    --category-status: #8C888C;
}

.pokemon-skills-container {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.abilities-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.abilities-container > .abilities {
    background-color: var(--pokeball-bot);
    padding: 5px;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden scroll;
    height: 20vh;
}
.abilities-container > .abilities::-webkit-scrollbar {
    display: none;
}
.abilities-container > .abilities + div {
    margin-left: 5px;
}
.abilities-container > .abilities > span {
    text-align: center;
}

.abilities-container > .abilities > span + span {
    margin-top: 5px;
    font-size: 0.8em;
}

.moves-container {
    margin-top: 10px;
    background-color: var(--pokeball-bot);
    border-radius: 3px;
    border-collapse: collapse;
    overflow: hidden scroll;
    flex: 1 1 auto;
}
.moves-table {
    width: 100%;
}
.moves-header {
    position: sticky;
    top: 0;
    background-color: var(--pokeball-bot);
}

.moves-table > tr:nth-child(even) {
    background-color: #d3d3d3;
}
.moves-table > tbody > .moves > td, .moves-header > td {
    padding: 5px;
}