.poke-details-container {
    height: 100vh;
    display: flex;
    flex-direction: row;
    background-size: 600% 200%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% { background-position: 0% 50%; }
	50% { background-position: 100% 50%; }
	100% { background-position: 0% 50%;	}
}

.details-left-section, .details-right-section {
    display: flex;
    flex-direction: column;
}
.details-left-section > .pokemon-title-container {
    margin-top: 20px;

}
.details-left-section > div {
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
}
.details-right-section > div {
    margin-right: 20px;
    margin-bottom: 20px;
}
