:root {
    --generation-i-bg: linear-gradient(90deg, #c60000 0%, #c9c501 50%, #005bc6 100%);
    --generation-ii-bg: linear-gradient(90deg, #90bcc2 0%, #c3a400 50%, #939393 100%);
    --generation-iii-bg: linear-gradient(90deg, #0c4398 0%, #3a5f0b 25%, #399357 50%, #aa4806 75%, #950614 100%);
    --generation-iv-bg: linear-gradient(90deg, #998102 0%, #9a9a8e 25%, #bababa 50%, #9ba3a4 76%, #4d909f 100%);
    --generation-v-bg: linear-gradient(90deg, #000000 0%, #afafaf 100%);
    --generation-vi-bg: linear-gradient(90deg, #6f0c15 0%, #b64000 33%, #29a77b 66%, #0a377e 100%);
    --generation-vii-bg: linear-gradient(90deg, #d76902 0%, #b98301 33%, #017bb6 66%, #3c0774 100%);
    --generation-viii-bg: linear-gradient(90deg, #074674 0%, #5435a4 33%, #a80061 66%, #9f0000 100%);
    --generation-others-bg: linear-gradient(90deg, #c79e01 0%, #249968 50%, #20578b 100%);
}

.pokemon-sprites-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.main-sprite-container, .pokemon-sprite > .sprite {
    background-size: contain;
    background-color: var(--pokeball-bot);
    background-repeat: no-repeat;
}
.main-sprite-container {
    width: 20vw;
    height: 20vw;
    border-radius: 4px;
    box-shadow: 0 0 8px black;
}

.sprites-section {
    position: relative;
    text-align: center;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    height: 20vw;
    flex: 1 1 auto;
}

.text-sprites {
    background: var(--pokeball-bot);
    border-radius: 2px;
    margin: 0px 5px 5px 5px;
    padding: 5px;
    box-shadow: 0 0 4px black;
}

.sprites-container {
    overflow: hidden scroll;
    flex: 1 1 auto;
}
.sprites-container::-webkit-scrollbar {
    width: 4px;
}
.sprites-container::-webkit-scrollbar-thumb {
    background-color: var(--pokeball-bot);
    border-radius: 20px;
}

.gen-sprite-container {
    padding: 5px;
}
.gen-sprite-container > .gen-title {
    border: 3px solid var(--pokeball-bot);
    padding: 5px;
    color: var(--pokeball-bot);
    border-radius: 4px;
    box-shadow: 0 0 4px black;
}

.pokemon-sprite {
    margin-top: 5px;
    display: table;
}
.pokemon-sprite > .sprite {
    height: 5vw;
    width: 5vw;
    border-radius: 4px;
    box-shadow: 0 0 4px black;
}
.pokemon-sprite > .name {
    color: var(--pokeball-bot);
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    font-size: 0.8em;
    text-shadow: 0 0 10px BLACK;
}
.pokemon-sprite > .name > .text {
    display: flex;
    flex-direction: column;
}
