.error-message-container, .pokemon-return-button {
    left: calc(40vw - 5px);
    position: absolute; 
    background-color: var(--pokeball-top-dark);
    color: white;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0px 0px 10px black;
    text-shadow: 0px 0px 2px black;
    text-align: center;
}
.error-message-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 20vw;
    height: 40vh;
    top: calc(20vh - 5px);
    font-size: 1.5em;
}

.pokemon-return-button {
    height: 5vh;
    width: 20vw;
    top: calc(60vh + 25px);
    font-size: 1.3em;
    cursor: pointer;
}