:root {
    --hp-stat-base: #A60000;
    --hp-stat-fill: #ff0000;
    --attack-stat-base: #9C531F;
    --attack-stat-fill: #f08030;
    --defense-stat-base: #A1871F;
    --defense-stat-fill: #f8d030;
    --special-attack-stat-base: #445E9C;
    --special-attack-stat-fill: #6890F0;
    --special-defense-stat-base: #4E8234;
    --special-defense-stat-fill: #78C850;
    --speed-stat-base: #A13959;
    --speed-stat-fill: #F85888;
}

.pokemon-stats-container {
    display: flex;
    flex-direction: row;
    margin: 20px 20px 20px 0px;
    padding: 5px;
    background-color: var(--pokeball-bot);
    border-radius: 5px;
    justify-content: space-between;
}
.pokemon-stats-container > div + div {
    margin-left: 5px;
}

.pokemon-power-container {
    height: calc(30vh);
    width: 100%;
    display: flex;
    flex-direction: column;
}

.power-base {
    width: 100%;
    height: 100%;
    border-radius: 3px;
    box-shadow: inset 0px 0px 5px black;
    display: flex;
    justify-content: center;
    position: relative;
    color: white;
    line-height: 2.5em;
}

.power-fill {
    width: calc(100% - 10px);
    position: absolute;
    bottom: 5px;
    transform: rotateZ(180deg);
}

.power-fill > .fill {
    border-radius: 3px;
    width: 100%;
    height: 100%;
    animation: fill 1s ease;
}

.power-info {
    text-align: center;
    margin-top: 10px;
    display: flex;
    color: black;
    flex-direction: column;
}

@keyframes fill {
    0% { height: 0%; }
    100% { height: 100%; }
}