:root {
    --pokeball-top: #ee1515;
    --pokeball-top-dark: #770b0b;
    --pokeball-bot: #f0f0f0;
    --pokeball-bot-dark: #787878;
    --pokeball-black: #222224;
}

@font-face { font-family: vcr-mono; src: url(./assets/vcr-mono.ttf); }

.pokemon-db-main-page {
    background: var(--pokeball-top);
    position: absolute;
    height: 100%;
    width: 100%;
    font-family: vcr-mono;
    animation: 2s pokemon-db-show ease;
}
@keyframes pokemon-db-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.pokeball-cluster {
    width: 100vw;
    height: 50vh;
    position: absolute;
    color: var(--pokeball-bot);
    text-align: center;
    font-size: 5em;
    line-height: 3.7em;
    animation-duration: 2s;
    animation-timing-function: ease;
}
.pokeball-cluster.top {
    background: var(--pokeball-top-dark);
    top: 0;
}
.pokeball-cluster.bot {
    background: var(--pokeball-bot-dark);
    top: 50vh;
}
.pokeball-cluster.top.open-cluster {
    animation-name: play-top-cluster;
}
.pokeball-cluster.bot.open-cluster {
    animation-name: play-bot-cluster;
}
@keyframes play-top-cluster {
    from { top: 0; }
    to { top: -50vh; }
}
@keyframes play-bot-cluster {
    from { top: 50vh; }
    to { top: 100vh; }
}

.poke-circle-hoverer, .poke-circle-container {
    width: calc(80vh + 40px);
    height: calc(40vh + 20px);
    position: absolute;
}
.poke-circle-hoverer {
    cursor: pointer;
    animation-duration: 2s;
    animation-timing-function: ease;
    z-index: 1;
}
.poke-circle-hoverer.top {
    top: calc(10vh - 20px);
}
.poke-circle-hoverer.bot {
    bottom: calc(10vh - 20px);
}
.poke-circle-hoverer.top.open-pokeball {
    animation-name: play-top-pokeball;
}
.poke-circle-hoverer.bot.open-pokeball {
    animation-name: play-bot-pokeball;
}
@keyframes play-top-pokeball {
    from { transform: rotateZ(0deg); left: calc(50vw - 40vh - 20px); }
    to { transform: rotateZ(90deg); left: 100vw; }
}
@keyframes play-bot-pokeball {
    from { transform: rotateZ(0deg); left: calc(50vw - 40vh - 20px); }
    to { transform: rotateZ(90deg); left: -100vw; }
}

.poke-circle-container {
    transition: top 0.7s;
    top: 1px;
}
.poke-circle-container.bot {
    top: -1px;
}
.poke-circle-container > .half-circle {
    width: 80vh;
    height: 40vh;
    border-radius: 50% 50% 50% 50% / 100% 100% 0% 0%;
    position: absolute;
    background: var(--pokeball-bot);
    border-style: solid;
    border-color: var(--pokeball-black);
    border-width: 20px 20px 0px 20px;
    z-index: 2;
}
.poke-circle-container > .half-circle.bot {
    bottom: 0%;
    border-radius: 50% 50% 50% 50% / 0% 0% 100% 100%;
    border-width: 0px 20px 20px 20px;
}

.poke-circle-container > .pokeball-line {
    height: calc(75vh - 10px);
    width: 200vw;
    position: absolute;
    left: calc(40vh + 20px - 100vw);
    transition: top 0.4s;
    z-index: 1;
    pointer-events: none;
}
.poke-circle-container > .pokeball-line.top {
    top: calc(-35vh + 20px);
    background: var(--pokeball-top);
    border-bottom: 10px solid var(--pokeball-black);
}
.poke-circle-container > .pokeball-line.bot {
    background: var(--pokeball-bot);
    border-top: 10px solid var(--pokeball-black);
}